import { Component, OnInit, ViewChild } from '@angular/core';
import { AuthenticationService } from 'src/app/services/authentication.service';
import { Router } from '@angular/router';
import { PopoverController, ActionSheetController, ModalController, IonInfiniteScroll } from '@ionic/angular';
import { BackendApiProvider } from 'src/providers/backend-api/backend-api.service';
import { Utils } from 'src/commons/Utils';
import { PageDataService } from 'src/providers/page-data/page-data.service';
import { NewPage } from './new/new.page';
import { TeamMenuPage } from './team-menu/team-menu.page';
import { Team } from 'src/models/Team';
import { ToastText, MenuText } from 'src/commons/Constants';

//-------------------------------------------------------------------------------------------------------------------------------//

@Component({
  selector: 'app-team',
  templateUrl: './team.page.html',
  styleUrls: ['./team.page.scss'],
})

//-------------------------------------------------------------------------------------------------------------------------------//

export class TeamPage {


  @ViewChild(IonInfiniteScroll) infiniteScroll: IonInfiniteScroll;

  //-------------------------------------------------------------------------------------------------------------------------------//

  private readonly logHeader = "TeamPage>>";

  //-------------------------------------------------------------------------------------------------------------------------------//

  private teams: Array<Team> = new Array<Team>();
  public teamList: Array<Team> = new Array<Team>();
  private searchTerm: string;
  private selectedUsers: Array<number> = new Array<number>();

  //-------------------------------------------------------------------------------------------------------------------------------//

  constructor(
    private authService: AuthenticationService,
    private router: Router,
    public popoverController: PopoverController,
    private api: BackendApiProvider,
    private utils: Utils,
    public actionSheetController: ActionSheetController,
    public modalController: ModalController,
    private pageDataService: PageDataService) {
    const logHeader = this.logHeader + "constructor>>";
    Utils.log(logHeader);
    this.getTeams();
    this.pageDataService.listUserEvent.subscribe((data) => {
      if (data == 'newData') {
        this.getTeams();
      }
    });
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  async getTeams() {
    const logHeader = this.logHeader + "getUsers>>";
    Utils.log(logHeader);
    this.api.getTeams().then((teams: Array<Team>) => {
      this.teams = teams;
      this.teamList = this.teams;
    }).catch((error) => {
      this.utils.simpleToastError(error + '\n' + ToastText.SOMETHING_WENT_WRONG);
    });
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  async abrirActionSheet(team: Team) {
    const actionSheet = await this.actionSheetController.create({
      header: MenuText.WHAT_DO_YOU_WANT_TO_DO,
      buttons: [
        {
          text: MenuText.EDIT,
          handler: () => {
            this.editTeam(team);
          }
        }, {
          text: MenuText.DELETE,
          role: 'destructive',
          handler: () => {
            this.api.removeTeam(team).then(() => {
              this.utils.simpleToast(ToastText.TEAM_REMOVED);
              this.teams.splice(this.teams.indexOf(team), 1);
              this.filter(this.searchTerm);
            }).catch(() => {
              this.utils.simpleToastError(ToastText.SOMETHING_WENT_WRONG);
            });
          }
        },
        {
          text: MenuText.CANCEL, role: 'cancel',
          handler: () => {
          }
        }]
    });
    await actionSheet.present();
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  filter(evt) {
    const logHeader = this.logHeader + "filter>>";
    Utils.log(logHeader);
    if (Utils.isVarInvalid(evt) || Utils.isVarInvalid(evt.srcElement)) {
      this.teamList = this.teams;
      return;
    }
    const searchTerm = evt.srcElement.value;
    this.searchTerm = searchTerm;
    if (!searchTerm) {
      this.teamList = this.teams;
      return;
    }
    this.teamList = this.teams.filter(n => n.getNome().toLowerCase().includes(searchTerm.toLowerCase()) || n.getLiderNome().toLowerCase().includes(searchTerm.toLowerCase()));
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  async openMenu(ev: any) {
    const popover = await this.popoverController.create({
      component: TeamMenuPage,
      event: ev,
      animated: true,
      showBackdrop: true,
      translucent: true,
      cssClass: 'popover_class',

    });
    return await popover.present();
  }

  async DismissClick() {
    await this.popoverController.dismiss();
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  async editTeam(team: Team = undefined) {
    const modal = await this.modalController.create({
      component: NewPage,
      animated: true,
      showBackdrop: false,
      cssClass: 'popover_class',
      backdropDismiss: false,
      componentProps: {
        team: team,
        admin: true,

      }
    });
    return await modal.present();
  }

  //-------------------------------------------------------------------------------------------------------------------------------//


  loadData(event) {
    setTimeout(() => {
      console.log('Done');
      event.target.complete();
      if (this.teamList.length == 1000) {
        event.target.disabled = true;
      }
    }, 500);
  }

}
